export const generateToken = (length = 10) => {
    return Math.floor(1000000000000000 + Math.random() * 9000000000000000).toString(36).substr(0, length)
}

export const perhitunganBeratBadanPria = (tinggi = 0) => {
    return (parseInt(tinggi) - 100) - ((parseInt(tinggi) - 100) * 0.1);
}

export const perhitunganBeratBadanWanita = (tinggi = 0) => {
    return (parseInt(tinggi) - 100) - ((parseInt(tinggi) - 100) * 0.15);
}